import { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import {  useContext } from "react";
//components
import "react-responsive-carousel/lib/styles/carousel.min.css"; // requires a loader
import { makeStyles } from "@material-ui/core/styles";
import Carousel from "react-bootstrap/Carousel";

//dependencies
import VideosItem from "./videosItem";
import VideosCategoriaFila2 from "./videosCategoriaFila2";
import VideosCategoriaFila from "./videosCategoriaFila";
import * as HistorialUserServer from "../../services/historialUser";
import * as PreferenciasUserServer from "../../services/preferenciasUser";
import Context from "../context/UserContext";
import VideoListSearch from "./videoListSearch";
import { Container, containerClasses } from "@mui/material";

const useStyles = makeStyles((theme) => ({
  root: {
  },
  "@media (max-width: 1248px)": {
    root: {
      display: "flex",
    },
  },
}));

/**
 * Componente para organizar en las pestañas (videos, series, casos) los videos en su respectiva categoria, con un buscador y filtro
 * @param {*} categories
 * @returns Contenedor con los videos y categorias 
 */
function VideosListUser2({videos, categories,webinarsCondition, ...props}) {
  const history = useNavigate();

  const [histUsers, setHistUsers] = useState();
  const [prefUsers, setPrefUsers] = useState(null); 
  const [categoriasTotales, setCategoriasTotales] = useState(null);
  const { user } = useContext(Context)

  /**
   * Función para obtener el historial de los usuarios.
   */
  const getHistorialUsers = async () => {
    try {
      const res = await HistorialUserServer.ListHistorialUser({'user_id':user.id});
      setHistUsers(res);
    } catch (error) {
      console.log(error);
    }
  };
  /**
   * Función para obtener las preferencias de los usuarios
   */
  const getPreferenciasUser = async () => {
    try {
      const res = await PreferenciasUserServer.ListPreferenciaUser({'user_id':user.id});   
      setPrefUsers(res);
    } catch (error) {
      console.log(error);
    }
  };
  
 
  
  /**
   * Función para organizar las categorias de videos dependiendo de las preferencias.
   */
  useEffect(() => {
    //console.log("webinarsCondition:", webinarsCondition);
   // console.log("webinarsCondition:", videos);

    if(prefUsers && categories) {
      let array = [];
      let array1 =[];
      let array2 = [];

      for (let i = 0; i < prefUsers.length; i++) {
        array1.push(prefUsers[i].categoria);        
      } 
      for (let i = 0; i < categories.length; i++) {
        array2.push(categories[i]);        
      }    
      
      for (var i = 0; i < array2.length; i++) {
          var igual=false;
           for (var j = 0; j < array1.length & !igual; j++) {
               if(array2[i].categoria === array1[j] )
                igual=true;
              }
              array = prefUsers
          if(!igual)array.push(array2[i]);
      }
      
      if (array) {
        setCategoriasTotales(array);
      }
      else {
        setCategoriasTotales(null);
      }
      
    }
  },[categories, prefUsers])
  /**
   * Función para redireccionar al reproductor del video seleccionado, se crea un nuevo historial de usuario si es la primera vez en ver el video, de lo contrario se envía este historial como state
   * @param {*} id 
   */
const verVideo = async (id, categories) => {
  //console.log("CATListUser2", categories);

  // Si no hay usuario logueado y el video es un webinar (categoría 3), redirige directo sin guardar historial
  if (!user?.id && categories === 13) {
    history(`/seeWebinar/${id}`);
    return;
  }

  // Si hay usuario logueado o no es webinar, registrar historial y redirigir
  if (user?.id) {
    const formData = new FormData();
    formData.append("usuario", user.id);
    formData.append("video", id);

    let histUser = null;

    try {
      // Verificar si ya existe historial para este usuario y video
      const existente = histUsers.find(
        (h) => h.usuario?.id === user.id && h.video === id
      );

      if (existente) {
        histUser = existente;
      } else {
        const hu = await HistorialUserServer.RegisterHistorialUser(formData);
        histUser = hu.data;
      }
    } catch (error) {
      console.error("Error registrando historial:", error);
    }

    // Redirigir al video con historial
    history(`/seeVideo/${id}`, { state: histUser });
  } else {
    // Si no hay usuario logueado y no es webinar, redirige directo a ver video sin historial
    history(`/seeVideo/${id}`);
  }
};

  
  
  const classes = useStyles();
  if (videos ) {
    return (
      <div className="">
  {webinarsCondition ? (
    // 🔷 Mostrar contenido para webinars (usa props.webinars)
    <div id="carousel_videos">
      <Carousel className={classes.root} id="carousel" fade>
        {videos &&
          props.search(videos).slice(0, 6).map((video, index) => (
            <Carousel.Item key={index}>
              <VideosItem
                key={index}
                video={video}
                listVideos={videos}
                histUsers={histUsers}
                user={user}
                verVideo={verVideo}
                webinars={videos}
              />
            </Carousel.Item>
          ))}
      </Carousel>

      <div id="card_videos" className="">
        {(prefUsers ? categoriasTotales : categories) &&
          props
            .search2(prefUsers ? categoriasTotales : categories)
            .map((element, index) =>
              props.search2(prefUsers ? categoriasTotales : categories)
                .length <= 1 ? (
                <VideoListSearch
                  key={index}
                  categoria={element}
                  videos={videos}
                  search={props.search}
                  histUsers={histUsers}
                  user={user}
                  verVideo={verVideo}
                />
              ) : (
                <VideosCategoriaFila
                  key={index}
                  categoria={element}
                  videos={videos}
                  search={props.search}
                  histUsers={histUsers}
                  user={user}
                  verVideo={verVideo}
                  webinarsCondition={webinarsCondition}
                  
                />
              )
            )}
      </div>
    </div>
  ) : (
    // 🔶 Mostrar contenido general (sin webinars)
    <div id="carousel_videos">
      {/* <h2>🕑 Últimos videos 2</h2> */}
      <Carousel className={classes.root} id="carousel" fade>
        {videos &&
          props.search(videos).slice(0, 6).map((video, index) => (
            <Carousel.Item key={index}>
              <VideosItem
                key={index}
                video={video}
                listVideos={props.listVideos}
                histUsers={histUsers}
                user={user}
                verVideo={verVideo}
              />
            </Carousel.Item>
          ))}
      </Carousel>

      <div id="card_videos" className="mt-5">
        {(prefUsers ? categoriasTotales : categories) &&
          props
            .search2(prefUsers ? categoriasTotales : categories)
            .map((element, index) =>
              props.search2(prefUsers ? categoriasTotales : categories)
                .length <= 1 ? (
                <VideoListSearch
                  key={index}
                  categoria={element}
                  videos={videos}
                  search={props.search}
                  histUsers={histUsers}
                  user={user}
                  verVideo={verVideo}
                />
              ) : (
                <VideosCategoriaFila
                  key={index}
                  categoria={element}
                  videos={videos}
                  search={props.search}
                  histUsers={histUsers}
                  user={user}
                  verVideo={verVideo}
                />
              )
            )}
      </div>
    </div>
  )}
</div>

    );
  }
  else {
    return (
      <div id="card_videos m-5 text-center">
        <h2 style={{color:"white"}}><center>No hay videos disponibles</center></h2>
      </div>
    )
  }
}

export default VideosListUser2;
